@charset "UTF-8";
.btn-primary {
  padding: 10px 15px;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid #919191;
  border-radius: 8px;
  background-color: #eeeded;
  color: #222;
  font-size: 0.9rem;
}
.btn-primary:hover {
  transition: 1s cubic-bezier(0.47, 0, 0.75, 0.72);
  background: #4f95da;
  color: #eeeded;
}

.body,
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

h1,
h2,
th {
  font-family: "Poppins", Verdana, Geneva, sans-serif;
  font-weight: 400;
  color: #222;
  font-size: 1.3rem;
  text-decoration: overline;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.flex-header {
  display: flex;
  flex-direction: row;
  background-color: #eeeded;
  column-gap: 90px;
  padding: 10px 0;
}

.flex-contact-info {
  margin: auto;
}
.flex-contact-info p,
.flex-contact-info a {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  color: #222;
  font-size: 0.8rem;
  line-height: 1;
  text-align: center;
}

.header-image {
  width: 33vw;
  height: 13vh;
  object-fit: cover;
}

nav {
  background: #222;
  padding: 0 15px;
}

.menu,
.submenu {
  list-style-type: none;
}

.item {
  padding: 10px;
}
.item a:hover::after {
  color: #ccc;
}

/* Mobile menu */
.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.menu li a {
  display: block;
  padding: 15px 5px;
  font-family: "Poppins";
  color: #eeeded;
}

.menu li.subitem a {
  padding: 15px;
}

.toggle {
  order: 1;
  font-size: 20px;
  margin-left: auto;
}

.item {
  order: 3;
  width: 100%;
  text-align: center;
  display: none;
}

.active .item {
  display: block;
  cursor: pointer;
}

.submenu {
  display: none;
}

.submenu-active {
  background-color: #111;
  border-radius: 3px;
}

.submenu-active .submenu {
  display: block;
}

.has-submenu i {
  font-size: 12px;
}

.has-submenu > a::after {
  font-family: "Font Awesome 5 Free";
  font-size: 12px;
  line-height: 16px;
  font-weight: 900;
  content: "ÔÅ∏";
  color: white;
  padding-left: 5px;
}

.subitem a {
  padding: 10px 15px;
}

.next-btn {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* Caption text */
.carausel-text {
  width: 100%;
  color: #f2f2f2;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  text-align: center;
}
.carausel-text p {
  font-size: 15px;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.services-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.services-table {
  display: flex;
  flex-direction: column;
  border-collapse: collapse;
  color: #222;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}
.services-table thead {
  font-family: "Poppins", Verdana, Geneva, sans-serif;
  font-weight: 400;
  color: #222;
  font-size: 1.3rem;
  text-decoration: overline;
  padding: 10px;
  text-decoration: overline;
  border-bottom: none;
}
.services-table tr {
  border-bottom: 1px dotted black;
}
.services-table td {
  padding: 10px;
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  color: #222;
}

.services-calc {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  color: #222;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  max-width: 320px;
  width: 100%;
  margin-top: 27px;
  padding-top: 40px;
  align-items: center;
  border-top: 1px solid black;
  background-color: gainsboro;
}
.services-calc-form {
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  height: 177px;
  justify-content: space-evenly;
  align-items: center;
  padding: 6px 20px;
  border: 1px solid black;
  margin-top: 10px;
}
.services-calc-form select {
  font-family: "Poppins";
  text-align: center;
  text-align-last: center;
  cursor: pointer;
  padding: 5px 10px;
}
.services-calc-form select:invalid {
  color: #919192;
}
.services-calc-form select option {
  color: #222;
}
.services-calc-form label {
  line-height: 0;
  padding: 15px 0;
}
.services-calc-form .calc-form-input {
  padding: 5px 10px;
  width: 70%;
  border-radius: 6px;
}
.services-calc button {
  margin-top: 20px;
}
.services-calc p {
  padding: 15px 0;
}

/* Tablet */
@media all and (min-width: 700px) {
  .flex-header {
    padding: 10px 30px;
  }

  .flex-contact-info {
    padding-top: 5%;
  }
  .flex-contact-info p,
.flex-contact-info a {
    font-size: 1.5rem;
  }

  .menu {
    justify-content: center;
  }

  .toggle {
    flex: 1;
    text-align: right;
    order: 2;
  }
}
/* Responsive Styles */
/* Tablet  */
@media all and (min-width: 700px) {
  .menu {
    justify-content: center;
  }

  .toggle {
    flex: 1;
    text-align: right;
    order: 2;
  }
}
/* Desktop  */
@media all and (min-width: 960px) {
  .flex-header {
    padding: 10px 30px;
  }

  .flex-contact-info p {
    font-size: 1.1rem;
  }

  .menu {
    align-items: flex-start;
    flex-wrap: nowrap;
    background: none;
  }

  .item {
    order: 1;
    position: relative;
    display: block;
    width: auto;
  }

  .submenu-active .submenu {
    display: block;
    position: absolute;
    left: 0;
    top: 68px;
    background: #111;
  }

  .toggle {
    display: none;
  }

  .submenu-active {
    border-radius: 0;
  }
}